.Blog h1 {
  margin-top: -0.5rem;
  text-align: center;
  font-style: italic;
}

.Blog {
  padding-top: 5rem;
  padding-bottom: 0rem;
}

.Blog .get-in-touch {
  text-align: center;
  margin-top: 4em;
  margin-bottom: 0em;
}

.Blog .contact {
  text-align: center;
}

@media only screen and (max-width: 412px) {
  .Blog .name {
    font-size: 2.8em;
  }
}

.listing {
  justify-content: center;
}

.paraHead {
  text-decoration: underline;
  padding: 10px;
  align-content: center;
}

a {
  text-decoration: none;
  display: inline-block;
  color: #000;
  overflow: visible;
}

p a::after {
  content: "";
  display: block;
  height: 11px;
  width: 96%;
  background: rgba(195, 154, 211, 0.565);
  margin-top: -11px;
  margin-left: 4px;
}

p a:visited {
  color: #000;
  text-decoration: none;
}
