@font-face {
  font-family: "Rotis Semi Sans Std";
  font-weight: bold;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Circular Std", -apple-system, BlinkMacSystemFont, "Helvetica",
    "Arial", sans-serif;
  font-size: 16px;
  font-feature-settings: "liga", "kern";
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  position: relative;
  background: white;
}

#root {
  padding-left: 2rem;
  padding-right: 2rem;
}

h1 {
  font-weight: 900;
  font-size: 4em;
  letter-spacing: 0.1rem;
}

h2 {
  font-weight: 900;
  font-size: 3em;
  margin: 0.33em 0;
}

h3 {
  font-weight: 500;
  font-size: 2em;
  margin-top: 0.5em;
  margin-bottom: 0.5em;
}

h4 {
  font-weight: bold;
  font-size: 1.2em;
  margin: 1em auto 0;
}

h5 {
  font-weight: bold;
  font-size: 1em;
  margin: 1em auto 0.5em;
}

p {
  line-height: 1.5em;
}

.no-margin-bottom {
  margin-bottom: 0 !important;
}

.no-margin-top {
  margin-top: 0 !important;
}

.one-margin-vert {
  margin: 1em auto;
}

.one-margin-top {
  margin-top: 1em;
}

.one-margin-bottom {
  margin-bottom: 1em;
}

a {
  text-decoration: none;
  display: inline-block;
  color: #000;
  overflow: visible;
}

p a::after {
  content: "";
  display: block;
  height: 11px;
  width: 96%;
  background: rgba(195, 154, 211, 0.565);
  margin-top: -11px;
  margin-left: 4px;
}

p a:visited {
  color: #000;
  text-decoration: none;
}

canvas {
  position: absolute !important;
  width: 100%;
  height: 100%;
  z-index: -1;
}
